.user {
    display: flex;
    align-items: center;
}

.icon {
    font-size: 2.2em;
    margin-left: 0.3em;
}

.user__image {
    width: 1.2em;
    height: 1.2em;
    margin-left: 10px;
    border-radius: 50%;
    font-size: 40px;
    background-position: center;
    background-size: cover;
}

.user__modal {
    background: white;
    outline: none;
    margin: 2em auto;
    width: 500px;
    max-width: calc(100vw - 2rem);
    max-height: calc(100vh - 2rem);
    overflow-y: auto;
    position: relative;
    box-shadow: 0 0 1rem rgba(41, 61, 113, .1);
    padding: 2em 3em;
    border-radius: 0.5em;
    cursor: default;
}

.user__icon-btn {
    background: transparent;
    padding: 0;
    border: none;
    margin-bottom: -0.4em;
    margin-left: 10px;
    transition: 0.2s;
}

.user__icon-btn:hover {
    color: #cccccc;
}