@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700&display=swap);
* {
    box-sizing: border-box;
}

body {

    margin: 0;
    font-family: 'Montserrat', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #f8faff;
    font-size: 14px;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.content {
    margin: 0 auto;
    padding: 0 20px;
    max-width: 1200px;
}


.btn {
    border: none;
    font-family: inherit;
    padding: 1em;
    cursor: pointer;
    opacity: 1;
    transition: 0.2s;
    width: 220px;
}

.btn:hover {
    opacity: 0.9;
}

.bg_green {
    background-color: #8CCA11;
    color: #ffffff;
}

.bg_red {
    background-color: #FB4949;
    color: #ffffff;
}

.bg_orange {
    background-color: #fe7a3c;
    color: #ffffff;
}

.separate {
    border: 1px solid rgba(0, 0, 0, 0.1);
    width: 100%;
    margin: 30px auto 30px;
}

.form {
    background-color: #ffffff;
    box-shadow: 0 0 1rem rgba(41, 61, 113, .1);
    padding: 2em 3em;
    margin: 3em auto;
    border-radius: 0.5em;
    width: 500px;
    text-align: center;
}


.form__btn {
    width: 100%;
    font-weight: 800;
    margin-bottom: 1em;
}

.form__header {
    margin: 0 0 1em;
    color: #253c77;
    font-weight: 800;
    text-align: left;
}

.form-overlay {
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    text-align: left;
}

.form__link {
    width: 500px;
    margin-bottom: 3em;
    box-shadow: 0 0 1rem rgba(41, 61, 113, 0.2);
    font-weight: 800;
    font-size: 1.2em;
}
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #253c77;
    padding: 15px 0;
}

.header__item {
    font-size: 1.4em;
    font-weight: 800;
}
.user {
    display: flex;
    align-items: center;
}

.icon {
    font-size: 2.2em;
    margin-left: 0.3em;
}

.user__image {
    width: 1.2em;
    height: 1.2em;
    margin-left: 10px;
    border-radius: 50%;
    font-size: 40px;
    background-position: center;
    background-size: cover;
}

.user__modal {
    background: white;
    outline: none;
    margin: 2em auto;
    width: 500px;
    max-width: calc(100vw - 2rem);
    max-height: calc(100vh - 2rem);
    overflow-y: auto;
    position: relative;
    box-shadow: 0 0 1rem rgba(41, 61, 113, .1);
    padding: 2em 3em;
    border-radius: 0.5em;
    cursor: default;
}

.user__icon-btn {
    background: transparent;
    padding: 0;
    border: none;
    margin-bottom: -0.4em;
    margin-left: 10px;
    transition: 0.2s;
}

.user__icon-btn:hover {
    color: #cccccc;
}
.profile{

}

.profile img{
    width: 100%;
}

.profile__btn{
}

.profile__buttons{
    display: flex;
    justify-content: space-between;
}
.account {
    display: grid;
    grid-template-rows: auto;
    grid-template-areas: "card card card select" "history history history .";
    color: #253c77;
    margin-top: 2em;
}


.account__select {
    grid-area: select;
}

.account__history {
    margin-top: 2em;
    grid-area: history;

}
.account__card {
    grid-area: card;
    background-color: #ffffff;
    box-shadow: 0 0 1rem rgba(41, 61, 113, .1);
    padding: 3em;
    margin-right: 2em;
    border-radius: 0.5em;
    max-width: 900px;
}

.account__buttons {
    display: flex;
    flex-wrap: wrap;
    margin: 15px 0;
    justify-content: space-between;
}

.account__number {
    font-size: 1.4em;
    font-weight: 400;

}

.account__balance {
    font-size: 3em;
    font-weight: 900;
    margin: 0.5em 0;
}

.account__other {
    display: flex;
    flex-wrap: wrap;
    margin: 15px 0;
    justify-content: space-between;
    align-items: center;
}

.account__select .btn {
    width: 100%;
}
.account__date {
    margin: 1em;
    padding: 0.5em;
    border-radius: 0.2em;
    border: 1px solid rgba(0, 0, 0, 0.2);
}

.account__table {
    background-color: #ffffff;
    box-shadow: 0 0 1rem rgba(41, 61, 113, .1);
    padding: 2em 3em;
    margin-right: 2em;
    border-radius: 0.5em;
    max-width: 900px;
}

.account__table table {
    width: 100%;
    border-collapse: collapse;
}

.account__table table tbody tr:nth-child(even) {
    background-color: #f8faff;
}

.account__table table thead {
    border-bottom: 2px solid rgb(41, 61, 113);
    font-size: 1.3em;
    font-weight: 800;
}

.account__table thead td {
    padding: 0.4em 1em;
}

.account__table tbody td {
    padding: 1.2em;
}

.input {
    display: block;
    width: 100%;
    background-color: #eff4ff;
    padding: 0.7em;
    font-size: 1.3em;
    font-weight: 600;
    color: #253c77;
    margin-bottom: 2em;
    border: 3px solid #fff;
    transition: 0.3s;
}

.input:focus {
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    border: 3px solid #253c77;
}

.input:invalid {
    border: 3px solid rgba(0, 0, 0, 0);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0);
    background-color: #fff1eb;
}

.label {
    display: block;
    margin: 0.7em 0;
    font-size: 1.2em;
    text-align: left;
    color: #253c77;
}
