.content {
    margin: 0 auto;
    padding: 0 20px;
    max-width: 1200px;
}


.btn {
    border: none;
    font-family: inherit;
    padding: 1em;
    cursor: pointer;
    opacity: 1;
    transition: 0.2s;
    width: 220px;
}

.btn:hover {
    opacity: 0.9;
}

.bg_green {
    background-color: #8CCA11;
    color: #ffffff;
}

.bg_red {
    background-color: #FB4949;
    color: #ffffff;
}

.bg_orange {
    background-color: #fe7a3c;
    color: #ffffff;
}

.separate {
    border: 1px solid rgba(0, 0, 0, 0.1);
    width: 100%;
    margin: 30px auto 30px;
}

.form {
    background-color: #ffffff;
    box-shadow: 0 0 1rem rgba(41, 61, 113, .1);
    padding: 2em 3em;
    margin: 3em auto;
    border-radius: 0.5em;
    width: 500px;
    text-align: center;
}


.form__btn {
    width: 100%;
    font-weight: 800;
    margin-bottom: 1em;
}

.form__header {
    margin: 0 0 1em;
    color: #253c77;
    font-weight: 800;
    text-align: left;
}

.form-overlay {
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    text-align: left;
}

.form__link {
    width: 500px;
    margin-bottom: 3em;
    box-shadow: 0 0 1rem rgba(41, 61, 113, 0.2);
    font-weight: 800;
    font-size: 1.2em;
}